@tailwind base;
@tailwind components;
@tailwind utilities;



.bg-dark-blue{
  background-color: #024c9696;
}

.bg-dark-blue:hover{
  background-color: #024c96;
}

.bg-blue-md{
  background-color: #42487481;
}

.bg-blue-md:hover{
  background-color: #424874;
}

.backdrop-blur-xsm{
  backdrop-filter: blur(2px);
}

.main{
  backdrop-filter: blur(8px);
  
  

}



::-webkit-scrollbar {
  width: 0px; /* Ancho de la barra de desplazamiento */
  border-radius: 0px;

}

::-webkit-scrollbar-track {
  background-color: transparent; /* Color del fondo de la barra de desplazamiento */
  border-radius: 0px;


}

::-webkit-scrollbar-thumb {
  border-radius: 0px; /* Radio de borde del pulgar */
}

.main, .about-me, .contact, .likes, .search {
  background-color: rgba(199, 199, 199, 0.555);
}



@media screen and (max-width:640px){
  .header{
    border-top: solid 1px rgba(255, 255, 255, 0.164);
    background-color: rgba(49, 49, 49, 0.74);
    height: 50px;
    z-index: 999999999999999999;
    
    position:fixed;
    bottom: 0;
  }
  .header_space{
    height: 40px;
    width: 100vw;
  }

  .main{
    width: 101%;
  }

  .blurx{
    backdrop-filter: blur(8px);
  }

}

@media screen and (min-width:640px) {
  .header{
    background-color: rgba(199, 199, 199, 0.315);
    border-radius: 12px;
  }

  ::-webkit-scrollbar {
    width: 5px; /* Ancho de la barra de desplazamiento */
    border-radius: 5px;
  
  }
  
  ::-webkit-scrollbar-track {
    background-color: transparent; /* Color del fondo de la barra de desplazamiento */
    border-radius: 5px;
  
  
  }
  
  ::-webkit-scrollbar-thumb {
    background-color: #88888893; /* Color del pulgar de la barra de desplazamiento */
    border-radius: 5px; /* Radio de borde del pulgar */
  }
}


.borde{
  border-color: rgba(255, 255, 255, 0.281);
}

.main-aside{
  border-left: solid rgba(255, 255, 255, 0.281) 1px;
}

.side__container-bottom{
  border-top: solid rgba(255, 255, 255, 0.281) 1px;

}



textarea{
  resize: none;
}

.main-aside{
  margin-left: 2px;
}

.modalEstilo{
  height: 50%;
  width: 40%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  position: relative;
  backdrop-filter: blur(8px);
  box-shadow: rgba(0, 0, 0, 0.329) 2px 2px 4px -1px ;
  border-radius: 16px;
}

.modalEstilo #close {
  position: absolute;
  top: -10px;
  right: -10px;
}